@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 5px;
    font-weight: light;
    color: white;
    font-size: 40px     ;
    text-align:center;
}

.subHeader{
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 3px;
    font-weight: 200;
    color: white;
    font-size: 15px;
    text-align:center;
}
.subHeader-2{
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 3px;
    font-weight: 800;
    color: white;
    font-size: 15px;
    text-align:center;
}

.hr{

    border-top: 1px solid white;
    background-color: black;
      
}
.bg-col{
    margin: auto;
    padding: 10px;  
    background-color: #2d3436;
    background-image: linear-gradient(315deg, #2d3436 0%, #000000 74%);
    background-attachment: fixed;
    background-size:cover;
    height:15vh;
    vertical-align: middle;
}



.navbar-nav{
    text-align : right; 
    background-color: #2d3436;
    background-image: linear-gradient(315deg, #2d3436 0%, #000000 74%);
    background-attachment: fixed;
    background-size:cover;
    height:5vh;
    vertical-align: middle;
    position : -webkit-sticky;
    position : sticky;
}

.footer{
    margin: auto;
    padding: 30px;  
    background-color: #2d3436;
    background-image: linear-gradient(315deg, #2d3436 0%, #000000 74%);
    background-attachment: fixed;
    background-size:cover;
    height:10vh;
    vertical-align: middle;
}

.left-box{
    width: 100%;
    margin: auto;
    padding:50px; 
    background-color: #2d3436;
    background-image: linear-gradient(315deg, #2d3436 0%, #000000 74%);
    background-repeat: no-repeat;
    height: 70vh;
    line-height:2.5;
}

.right-box{
    width: 100%;
    padding: 50px;
    background-color: #2d3436;
    background-image: linear-gradient(315deg, #2d3436 0%, #000000 74%);
    height: 60vh;
    line-height:1.7;
    background-position: center;
}
.title-text{
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 3px;
    font-weight: 500;
    color: white;
    font-size: 25px;
    text-align:left;
    padding: 15px;
}
.title-text-2{
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 3px;
    font-weight: 500;
    color: white;
    font-size: 25px;
    text-align:center;
    padding: 15px;
}
.about-text-box{
    width:60%;
}
.about-desc{
    padding: 20px;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 1.8px;
    font-weight: 200;
    color: white;
    font-size: 15px;

}

.logo-text{
    padding:20px;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 1.8px;
    font-weight: 200;
    color: white;
    font-size: 15px;
    text-align:center;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
}

.image-style{
    width:25%;
    float: right;
    padding: 10px; 
    position: relative;
    top: -300px;
    left: -150px;
}
.showcase-box{
    width: 100%;
    margin: auto;
    padding: 5px;
    background-color: #e9e8e2;
    height: 85vh;
    line-height:2.5;
}
.image-style-show{
    width:60%;
    float:center;
    position: relative;
    left: 315px;
}
.title-text-show{
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 3px;
    font-weight: 500;
    color: black;
    font-size: 25px;
    text-align:center;
}

.desc-text-show{
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 2px;
    font-weight: 500;
    color: black;
    font-size: 15px;
    text-align:center;
    padding: 18px;  
}
.responsive {
    max-width: 100%;
    height: auto;
  }
